import CloseIcon from '@mui/icons-material/Close';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { Box, Button, Dialog, DialogContent, DialogTitle, FormControl, IconButton, List, ListItemButton, ListItemIcon, ListItemText, Menu, MenuItem, Popover, Select, SelectChangeEvent, TextField, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useEffect, useRef, useState } from 'react';

export function LessonSelect(props: {
  selectedLesson?: string,
  lessons: string[],
  onChange: (value: string) => void,
  onExport: (collection: string, level: string) => void,
  disabled?: boolean,
}) {
  const classes = useStyles();
  const [fillText, setFillText] = useState('');
  const [selected, setSelected] = useState(props.selectedLesson);
  const [open, toggle] = useState(false);
  const listRef = useRef<HTMLUListElement>(null);

  const [downloadSet, setDownloadSet] = useState('KNTT_10');

  useEffect(() => {
    setSelected(props.selectedLesson);
  }, [props.selectedLesson]);

  return (
    <div>
      <Button disabled={props.disabled} fullWidth variant="outlined" color="inherit" className={classes.recipientButton} onClick={() => handleOpenDialog()}>
        <ListItemText>
          <Typography className={classes.textEllipsis} title={props.selectedLesson}>{props.selectedLesson}</Typography>
        </ListItemText>
        <ListItemIcon sx={{ justifyContent: 'end' }}>
          <KeyboardArrowDownIcon fontSize="large" />
        </ListItemIcon>
      </Button>
      <Dialog sx={{ zIndex: theme => theme.zIndex.modal +1 }} PaperProps={{
        sx: {
          paddingX: 2,
          paddingY: 1,
          '& .MuiDialogTitle-root': {
            paddingX: 0,
            paddingY: 2
          }
        }
      }} fullWidth={true} open={open} scroll="paper" onClose={() => toggle(false)}>
        <DialogTitle display="flex" justifyContent="space-between" fontWeight="bold">
          Lessons
          <Select
             labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={downloadSet} onChange={changeDownloadSet}>
            <MenuItem value ="KNTT_10">KNTT-10</MenuItem>
            <MenuItem value ="KNTT_11">KNTT-11</MenuItem>
            <MenuItem value ="KNTT_12">KNTT-12</MenuItem>
            <MenuItem value ="CTST_10">CTST-10</MenuItem>
            <MenuItem value ="CTST_11">CTST-11</MenuItem>
            <MenuItem value ="CTST_12">CTST-12</MenuItem>
            <MenuItem value ="CD_10">CD-10</MenuItem>
            <MenuItem value ="CD_11">CD-11</MenuItem>
            <MenuItem value ="CD_12">CD-12</MenuItem>
          </Select>
          <Button variant='contained' color='primary' title="Download" onClick={downloadClick}>
            Download
          </Button>
          <IconButton title="Close" onClick={() => toggle(false)}>
            <CloseIcon fontSize="small" />
          </IconButton>
        </DialogTitle>
            <Box paddingY={1}>
              <FormControl fullWidth>
                <TextField autoFocus={true} value={fillText} onChange={e => setFillText(e.target.value)} size="small" variant="outlined" placeholder="lesson.." />
              </FormControl>
            </Box>
            <DialogContent sx={{ height: '60vh', padding: 0 }}>
              <List ref={listRef}>
                {props.lessons.map(lesson =>
                  <ListItemButton key={lesson} selected={lesson == selected} className={classes.recipientItem} divider={true}
                  onClick={() => handleRecipientSelect(lesson)}>
                    <ListItemText>
                      <Typography className={classes.textEllipsis} title={lesson}>{lesson}</Typography>
                    </ListItemText>
                  </ListItemButton>
                )}
              </List>
            </DialogContent>
      </Dialog>
    </div>
  );

  async function handleOpenDialog() {
    toggle(true);
    setFillText('');
  }

  function handleRecipientSelect(address: string) {
    props.onChange(address);
    toggle(false);
  }

  function downloadClick(e: React.MouseEvent<HTMLElement>) {
   const parts = downloadSet.split('_');
   props.onExport(parts[0], parts[1]); 
  }

  function changeDownloadSet(e: SelectChangeEvent<string>) {
    setDownloadSet(e.target.value);
  }
}

const useStyles = makeStyles(theme => ({
  recipientButton: {
    textTransform: 'none',
    textAlign: 'left',
    display: 'flex',
    borderColor: '#a9a9a9'
  },
  recipientItem: {
    '&.Mui-selected': {
      backgroundColor: '#1976d266'
    },
    '&.Mui-selected:hover': {
      backgroundColor: '#1976d280'
    },
  },
  textEllipsis: {
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  },
  messageCount: {
    width: '100%',
    textAlign: 'end',
    paddingRight: 10
  }
}));