export interface QuestionItem {
    collection: string,
    scope: string,
    section: string,
    lesson: string,
    questionID: string,
    context?: string,
    questionContent: string,
    questionType: 'choice' | 'essay',
    options?: string,
    selectedOption?: string,
    conciseAnser?: string,
    explanation?: string,
}

interface JsonQuestionItem {
    Collection: string,
    Level: string,
    Section: string,
    Lesson: string,
    Scope: string,
    QuestionID: string,
    Context: string,
    Question: string,
    Type: string,
    Options: string,
    SelectedOption?: string,
    ConciseAnswer?: string,
    Explanation?: string
}

const data_files = [
    '/collect/KNTT/KNTT-10.json',
    '/collect/KNTT/KNTT-11.json',
    '/collect/KNTT/KNTT-12.json',
    '/collect/CTST/CTST-10.json',
    '/collect/CTST/CTST-11.json',
    '/collect/CTST/CTST-12.json',
    '/collect/CD/CD-10.json',
    '/collect/CD/CD-11.json',
    '/collect/CD/CD-12.json',
]
export async function readDataFiles(): Promise<QuestionItem[]> {
    let jQuestionItems: JsonQuestionItem[] = [];
    for (const file of data_files) {
        try {
            const res = await fetch(file);
            if (res.ok) {
                const jQuestions = await res.json() as JsonQuestionItem[];
                jQuestionItems = jQuestionItems.concat(jQuestions);
            }
        }
        catch {
            // ignore err
        }
    }
    
    const questions = jQuestionItems.map(function(jItem: JsonQuestionItem): QuestionItem {
        const question: QuestionItem = {
            collection: jItem.Collection,
            scope: jItem.Scope,
            section: jItem.Section,
            lesson: `${jItem.Collection}_${jItem.Level}_${jItem.Lesson}`,
            questionID: jItem.QuestionID,
            context: jItem.Context,
            questionContent: jItem.Question,
            questionType: jItem.Type.toLowerCase() == 'choice' ? 'choice' : 'essay',
            options: jItem.Options,
            selectedOption: jItem.SelectedOption,
            conciseAnser: jItem.ConciseAnswer,
            explanation: jItem.Explanation
        };
        return question;
    });
    return questions
}

export function exportFiles(allQuestions: QuestionItem[], collection: string, level: string): void {
    const jQuestions = toJsonQuestions(allQuestions);

    const data = jQuestions.filter(x => x.Collection == collection && x.Level == level);
    downloadJSON(data, `${collection}-${level}.json`);

}

function toJsonQuestions(questions: QuestionItem[]): JsonQuestionItem[] {
    return questions.map(function(question): JsonQuestionItem {
        const parts = question.lesson.split('_')
        return {
            Collection: parts[0],
            Level: parts[1],
            Scope: question.scope,
            Section: question.section,
            Lesson: parts[2],
            Context: question.context ?? '',
            QuestionID: question.questionID,
            Question: question.questionContent,
            Type: question.questionType,
            Options: question.options ?? '',
            SelectedOption: question.selectedOption ?? '',
            ConciseAnswer: question.conciseAnser ?? '',
            Explanation: question.explanation ?? ''
        };
    });
}

function downloadJSON(data: JsonQuestionItem[], filename: string) {
  // Chuyn object thnh string JSON
  const jsonStr = JSON.stringify(data, null, 2); // indent 2 spaces

  // To blob t string
  const blob = new Blob([jsonStr], { type: "application/json" });

  // To URL tm thi
  const url = URL.createObjectURL(blob);

  // To <a> n  download
  const a = document.createElement("a");
  a.href = url;
  a.download = filename;
  a.click();

  // Gii phng URL
  URL.revokeObjectURL(url);
}