import { Box, List, ListItemButton, ListItemIcon, ListItemText, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useRef} from 'react';
import { QuestionItem } from '../api';

export function QuestionList(props: {
  selectedIndex?: number,
  questionCollection: QuestionItem[],
  select: (index: number) => void,
}){
  const classes = useStyles();
  const listRef = useRef<HTMLUListElement>(null);

  return (
    <Box className={classes.boxMessages}>
      <Box> {/*  use to scroll */}
        <List ref={listRef}>
          {props.questionCollection.map((question, index) =>
            <ListItemButton key={question.questionID} autoFocus={index === props.selectedIndex} selected={index === props.selectedIndex} divider={true} 
            className={classes.messageItem + ' ' + (!question.explanation ? classes.highlight : '')} onClick={() =>{props.select(index);} }>
              <ListItemText>
                <Typography className={classes.messageSubject} fontSize="small">{question.questionID}</Typography>
                <Typography className={classes.extractContent} fontSize="small">{question.questionContent}</Typography>
              </ListItemText>
              <ListItemIcon className={classes.messageActions}>
              </ListItemIcon>
            </ListItemButton>
          )}
        </List>
      </Box>
    </Box>
  );
}

const useStyles = makeStyles({
  
  boxMessages: {
    flex: '1 1',
    overflowX: 'hidden',
    overflowY: 'scroll',
    paddingTop: 0,
    '& .MuiList-root': {
      paddingTop: 0
    },
    position: 'relative',
    '& .MuiBox-root': {
      position: 'absolute',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0
    }
  },
  messageItem: {
    paddingLeft: 8,
    '&.Mui-selected': {
      backgroundColor: '#1976d266'
    },
    '&.Mui-selected:hover': {
      backgroundColor: '#1976d280'
    },
    '&:hover $deletedIcon': {
      display: 'block'
    }
  },
  highlight: {
    backgroundColor: 'pink!important'
  },
  messageIcon: {
    '&.MuiListItemIcon-root': {
      minWidth: 'auto',
      marginRight: 4,
      alignSelf: 'start'
    }
  },
  messageSubject: {
    overflow: 'hidden',
  },
  extractContent: {
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  },
  messageActions: {
    display: 'flex',
    justifyContent: 'flex-end',
    '&.MuiListItemIcon-root': {
      minWidth: 30
    }
  },
  deletedMessage: {
    textDecoration: 'line-through!important'
  },
  deletedIcon: {
    display: 'none',
    marginRight: 4,
  }
});