import AttachmentIcon from '@mui/icons-material/Attachment';
import { Box, Button, CardContent, Divider, FilledInput, FormControl, TextareaAutosize, TextField, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useEffect, useState } from 'react';
import { useRef } from 'react';
import { QuestionItem } from '../api';

export function QuestionContent(props: {
  question: QuestionItem,
  updateQuestion: (newQs: QuestionItem) => void,
}) {

  const classes = useStyles();
  // const [question, setQuestion] = useState(props.question.questionContent);
  const [answer, setAnswer] = useState(props.question.conciseAnser);
  const [explanation, setExplanation] = useState(props.question.explanation);

  useEffect(() => {
    // setQuestion(props.question.questionContent);
    setAnswer(props.question.conciseAnser);
    setExplanation(props.question.explanation);
  }, [props.question.lesson, props.question.questionID]);

  return (
    <CardContent className={classes.content}>
      <Box display="flex" flexDirection="column" sx={{ flex: '1 1' }}>
        <Box className={classes.messageBody}>
          <Box className={classes.bodyLeft}>
            <Typography className={classes.subject}>{props.question.questionID}</Typography>
            <Box display="flex" marginBottom={1}>
              {props.question.context && (
                <Typography fontSize="small" sx={{ whiteSpace: 'pre-wrap' }}>{props.question.context}</Typography>
              )}
            </Box>
            {/* <TextareaAutosize value={question ?? ''} onChange={exChange} 
              className={classes.textExplanation} maxRows={20} id="question"/> */}
            <Typography fontSize="small" sx={{ whiteSpace: 'pre-wrap' }}>{props.question.questionContent}</Typography>
            <br/>
            <Typography fontWeight="bold" fontSize="small">Type: {props.question.questionType}</Typography>
            <br/>
            {props.question.options && (
              <Typography fontSize="small" sx={{ whiteSpace: 'pre-wrap' }}>{props.question.options}</Typography>  
            )}
          </Box>
          <Box className={classes.bodyRight}>
              <TextField value={answer ?? ''} onChange={answerChange} id="Concise Answer" label="Concise Answer" fullWidth />
              <Box display="flex" flexDirection="column" flex="1 1" marginTop={2}>
                <Typography fontSize="small" sx={{ whiteSpace: 'pre-wrap' }}>Explanation:</Typography>  
                <TextareaAutosize value={explanation ?? ''} onChange={exChange} 
                  className={classes.textExplanation} maxRows={40} id="Explanation"/>
                </Box>
              <Box>
                <Button type="submit" variant='contained' color='primary' onClick={handleUpdate}>Update</Button>
              </Box>
          </Box>
        </Box>
      </Box>
    </CardContent>
  );

  function answerChange(e: React.ChangeEvent<HTMLInputElement>) {
    setAnswer(e.target.value);
  }

  function exChange(e: React.ChangeEvent<HTMLTextAreaElement>) {
    setExplanation(e.target.value);
  }

  function handleUpdate() {
    const newQuestions = { ...props.question };
    newQuestions.conciseAnser = answer;
    newQuestions.explanation = explanation;
    props.updateQuestion(newQuestions);
  }
}

const useStyles = makeStyles({
  content: {
    flex: '1 1',
    display: 'flex',
    flexDirection:'column'
  },
  subject: {
    fontSize: 'large',
    fontWeight: 'bold',
    marginBottom: 8,
  },
  sender: {
    marginBottom: 8,
  },
  recipients: {
    marginBottom: 8,
    display: 'flex'
  },
  recipientTo: {
    flex: '1 1',
    overflowWrap: 'anywhere'
  },
  receiveTime: {
    marginLeft: 8,
    minWidth: 'max-content'
  },
  attachments: {
    marginTop: 8,
    marginBottom: 8,
  },
  attachmentButton: {
    textTransform: 'none',
    padding: 4,
    paddingLeft: 8,
    paddingRight: 8,
    margin: 4
  },
  messageBody: {
    marginTop: 16,
    flex: '1 1',
    display: 'flex',
  },
  bodyLeft: {
    width: '30%',
    minWidth: 350,
    '& .MuiDrawer-paper': {
        width: '30%',
        boxSizing: 'border-box'
    },
    borderRight: 1,
    display: 'flex',
    flexDirection: 'column',
    borderRightWidth: 1,
    borderRightColor: '#a9a9a9',
    borderRightStyle: 'double'
  },
  bodyRight: {
    padding: 12,
    display: 'flex',
    flex: '1 1',
    flexDirection: 'column'
  },
  iframeBody: {
    flex: '1 1',
    minHeight: '100%',
    borderStyle: 'none',
  },
  textExplanation: {
    flex: '1 1',
    maxHeight: 500,
    // overflow: 'scroll'
  }
});