import { Box, Card, Toolbar } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { useEffect, useReducer, useRef } from "react";
import { LessonSelect } from "./LessonSelect";
import { QuestionList } from "./QuestionList";
import { QuestionContent } from "./QuestionContent";
import { exportFiles, QuestionItem, readDataFiles } from "../api";

export function DataCollectPage() {
    const [state, dispatch] = useReducer(reducer, initialState);
    const classes = useStyles();
    useEffect(() => {
        load();
    }, []);

    // auto refresh
    setInterval(() => {
        autoSave();
    }, 300_000);  

    const coverRef = useRef<HTMLDivElement>(null);
    return (
        <div ref={coverRef}>
            <Box display="flex" flexDirection="column" sx={{ position: 'fixed', top: 0, bottom: 0, left: 0, right: 0 }}>
                <Box flex="1 1" display="flex" >
                    <Box className={classes.boxLeft}>
                        <Box padding={1} bgcolor="#fff">
                            <LessonSelect onExport={onExport} selectedLesson={state.selectedLesson} lessons={state.lessons} onChange={handleChangeSelectedLesson} />
                        </Box>
                        <QuestionList selectedIndex={state.selectedQsIndex} questionCollection={state.questions}
                            select={handleSelectQuestion} />
                    </Box>
                    <Box className={classes.messageContent}>
                        <Card>
                        {state.selectedQsIndex != null && state.questions[state.selectedQsIndex] &&
                            <QuestionContent question={state.questions[state.selectedQsIndex]} updateQuestion={handleUpdateQuestion} />
                        }
                        </Card>
                    </Box>
                </Box>
            </Box>
        </div>
    );

    async function load() {
        const allQuestions = await readDataFiles();
        const allLessons = allQuestions.map(x => x.lesson);
        const setLessons = new Set(allLessons);
        const lessons = Array.from(setLessons);
        dispatch(actions.load(lessons, allQuestions));
    }

    function handleChangeSelectedLesson(newLesson: string) {
        dispatch(actions.changeLesson(newLesson));
    }

    function handleSelectQuestion(newQuestionIndex: number) {
        dispatch(actions.selectQuestion(newQuestionIndex));
    }

    function handleUpdateQuestion(newQuestion: QuestionItem) {
        dispatch(actions.updateQuestion(newQuestion));
    }

    function autoSave() {

    }

    function onExport(collection: string, level: string) {
        exportFiles(state.all_questions, collection, level);
    }
}

interface PageState {
  loaded: boolean,
  lessons: string[], // full list of lessons
  all_questions: QuestionItem[], // full list of questions
  selectedLesson?: string,
  selectedQsIndex?: number,
  questions: QuestionItem[],
}

interface Action {
  type: 'LOAD' | 'CHANGE_LESSON' | 'SELECT_QUESTION' | 'UPDATE_QUESTION'
  data?: any
}
const initialState: PageState = {
  loaded: false,
  all_questions: [],
  lessons: [],
  questions: [],
}

const actions = {
  load: (lessons: string[], all_questions: QuestionItem[]): Action => ({
    type: 'LOAD',
    data: { lessons, all_questions }
  }),
  changeLesson: (newLesson: string): Action => ({
    type: 'CHANGE_LESSON',
    data: { newLesson }
  }),
  selectQuestion: (newIndex: number): Action => ({
    type: 'SELECT_QUESTION',
    data: { newIndex }
  }),
  updateQuestion: (newQuestion: QuestionItem): Action => ({
    type: 'UPDATE_QUESTION',
    data: { newQuestion }
  }),
};

function reducer(state: PageState, action: Action): PageState {
    switch (action.type) {
        case 'LOAD': {
            const {lessons, all_questions} = action.data;
            return { ...state, lessons, all_questions };
        }
        case 'CHANGE_LESSON': {
            const { newLesson } = action.data;
            const newQuestionList = state.all_questions.filter(x => x.lesson === newLesson);
            return { ...state, selectedLesson: newLesson, questions: newQuestionList, selectedQsIndex: undefined };
        }
        case 'SELECT_QUESTION': {
            const { newIndex } = action.data;
            return { ...state, selectedQsIndex: newIndex };
        }
        case 'UPDATE_QUESTION': {
            const { newQuestion } = action.data as { newQuestion: QuestionItem };
            const findQuestion = state.all_questions.find(x => x.lesson === state.selectedLesson && x.questionID == newQuestion.questionID);
            if (findQuestion) {
                findQuestion.context = newQuestion.context;
                findQuestion.questionContent = newQuestion.questionContent;
                findQuestion.conciseAnser = newQuestion.conciseAnser;
                findQuestion.explanation = newQuestion.explanation;
            }
            return { ...state };
        }
    }
}

const useStyles = makeStyles({
    boxLeft: {
        width: '20%',
        minWidth: 350,
        '& .MuiDrawer-paper': {
            width: '20%',
            boxSizing: 'border-box'
        },
        borderRight: 1,
        display: 'flex',
        flexDirection: 'column',
        borderRightWidth: 1,
        borderRightColor: '#a9a9a9',
        borderRightStyle: 'double'
    },
    hideReadEmail: {
        flex: '1 1',
        display: 'flex',
        justifyContent: 'flex-end'
    },
    messageContent: {
        flex: '1 1',
        '& .MuiPaper-root': {
            height: '100%',
            display: 'flex',
            flexDirection: 'column'
        }
    },
});
